/*--------------------------------------------------------------
1. GENERAL
---------------------------------------------------------------*/

body {
  background-color: #fff;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #2c4a6b;
  padding: 0;
  margin: 0;
  overflow: hidden;
}
a {
  color: #efbb20;
  outline: 0 none;
  text-decoration: none;
}
a:hover,
a:focus {
  text-decoration: none;
  outline: 0 none;
}
a:active,
a:hover {
  color: #333333;
}
p a {
  color: #333333;
}
img {
  border-style: none;
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}
hr {
  clear: both;
}
section,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
summary {
  display: block;
}
address {
  font-style: italic;
}
table {
  background-color: transparent;
  width: 100%;
}
table thead th {
  color: #333;
}
table td {
  padding: 15px 10px;
}
p,
address,
pre,
hr,
ul,
ol,
dl,
dd,
table {
  margin-bottom: 24px;
}
.overflow-hide {
  overflow: hidden;
}
.overflow-auto {
  overflow: auto;
}
.clear {
  clear: both;
}
.pull-none {
  float: none;
}
::-moz-selection {
  background: #3396d1;
  color: #fff;
}
::selection {
  background: #3396d1;
  color: #fff;
}
