.list-num-count > li:before {
  content: counter(li, decimal);
  counter-increment: li;
  position: absolute;
  top: 0;
  left: -28px;
  width: 28px;
  height: 28px;
  padding: 5px;
  color: #fff;
  background: #efbb20;
  font-weight: bold;
  text-align: center;
  font-size: 12px;
  border-radius: 100px;
}
.list-num-count.lower-alpha > li:before {
  content: counter(li, lower-alpha);
