/*===================== 
	Primary bg color 
=====================*/

/* Hex */
#loading-area > div,
.about-sidebox .closebtn,
.menuleft:after,
.bg-primary,
a.bg-primary:hover,
.overlay-primary-light:after,
.overlay-primary-middle:after,
.overlay-primary-dark:after,
.site-button,
.lg-actions .lg-next,
.lg-actions .lg-prev,
.lg-sub-html,
.lg-toolbar {
  background-color: #185993;
}

/*Hex color :hover */
.about-sidebox .closebtn:hover,
.site-button:active,
.site-button:hover,
.site-button:focus,
.active > .site-button,
.bg-primary-dark,
.sidenav .closebtn:hover,
.style-2 .dez-social-icon.dez-border li a:hover {
  background-color: #2376c0;
}

/*===================== 
	Premery text color 
=====================*/
a,
.text-primary,
.site-button.outline,
.dez-countdown .date .time,
.lg-outer .lg-toogle-thumb,
.lg-outer .lg-toogle-thumb:hover {
  color: #185993;
}

/*===================== 
	Border Color color 
=====================*/
.subscribe-box .form-control,
.site-button.outline,
.dez-countdown .date .time {
  border-color: #185993;
}

.lg-outer .lg-thumb-item.active,
.lg-outer .lg-thumb-item:hover {
  border-color: #185993;
}

/* GD */
.contact-box .form-group:after,
.count-1.countdown .date .time {
  background-image: linear-gradient(
    to right,
    #185993 0,
    #236dae 51%,
    #185993 100%
  );
  background-size: 200%;
  box-shadow: 10px 10px 30px 0 rgba(24, 89, 147, 0.25) !important;
}
.dez-social-icon li a,
.site-button.gradient {
  background-image: linear-gradient(to right, #fff 0, #000 51%, #fff 100%);
  background-size: 200%;
}
.style-1 .dez-social-icon li a:hover,
.site-button.gradient:hover {
  background-position: right center;
}
#loading-area > div,
.site-button.shadow {
  box-shadow: 10px 10px 30px 0 rgba(24, 89, 147, 0.25) !important;
}
