/* =========
	Typeogrphy
=========== */
.font-poppins h1,
.font-poppins h2,
.font-poppins h3,
.font-poppins h4,
.font-poppins h5,
.font-poppins h6,
.font-poppins {
  font-family: "Poppins", sans-serif;
}
.font-roboto {
  font-family: "Roboto", sans-serif;
}
.font-montserrat {
  font-family: "Montserrat", sans-serif;
}
.font-lora {
  font-family: "Lora", serif;
}
.font-barlow {
  font-family: "Barlow", sans-serif;
}

/* Coming Soon */
.dez-coming-soon {
  position: relative;
  height: 100vh;
  width: 100vw;
  background-size: cover;
  background-position: center;
}
.dez-coming-soon .container {
  position: relative;
}
.dez-coming-bx {
  width: 100%;
}

/* Countdown Style 1 */
.countdown .date .time {
  font-size: 90px;
  font-weight: 600;
  line-height: 90px;
  margin-bottom: 5px;
}
.countdown .date {
  color: #000;
  padding-right: 30px;
  display: inline-block;
}
.countdown .date span {
  display: block;
  font-size: 17px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.count-1.countdown .date .time {
  font-size: 70px;
  font-weight: 400;
  margin-bottom: 6px;
  background-color: #b9bec7;
  color: #fff;
  width: 110px;
  height: 115px;
  border-radius: 12px;
  text-align: center;
  line-height: 115px;
}
.count-1.countdown .date {
  padding-left: 18px;
  padding-right: 18px;
  position: relative;
}
.count-1.countdown .date:after {
  content: ":";
  font-size: 60px;
  position: absolute;
  right: -9px;
  top: 0;
  line-height: 115px;
  width: 15px;
  color: #b9bec7;
}
.count-1.countdown .date:last-child:after {
  content: none;
}
.countdown-timer {
  padding-bottom: 30px;
  float: center;
}
.clock-item {
  width: 155px;
  position: relative;
  margin: 0 10px;
}
.countdown-timer .clock .text {
  position: absolute;
  top: 0;
  width: 100%;
}
.countdown-timer .clock .type-time {
  text-transform: uppercase;
  color: #fff;
  font-size: 16px;
  letter-spacing: 2px;
  text-align: center;
  margin-bottom: 0;
}
.countdown-timer .clock .kineticjs-content {
  position: relative;
}
.countdown-timer .clock .kineticjs-content:after {
  content: "";
  position: absolute;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 100%;
  top: 2px;
  bottom: 2px;
  left: 2px;
  right: 2px;
  z-index: -1;
}
.countdown-timer .clock .val {
  font-size: 70px;
  color: #fff;
  width: 100%;
  line-height: 155px;
  text-align: center;
  margin-bottom: 15px;
}
.countdown-timer .clock {
  display: flex;
}
/* Position */
.posi-center,
.bottom-right,
.bottom-left,
.top-right,
.top-left {
  position: fixed;
  z-index: 2;
  display: block;
}

.bottom-right {
  bottom: 60px;
  right: 60px;
}
.bottom-left {
  bottom: 60px;
  left: 60px;
}
.top-right {
  top: 60px;
  right: 60px;
}
.top-left {
  top: 60px;
  left: 60px;
}

.posi-center {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 100%;
}
.dez-align .top-right,
.dez-align .bottom-right {
  right: 10px;
}
/* sidenav-menu */
.sidenav-menu {
  position: absolute;
  left: 0;
  height: 100%;
  width: 90px;
  border-right: 1px solid rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 20px 20px;
  justify-content: space-between;
}
.menu-btn a {
  position: relative;
  width: 40px;
  height: 22px;
  display: flex;
  justify-content: space-between;
  flex: unset;
  flex-direction: column;
}
.menu-btn a span {
  height: 2px;
  background: #000;
  display: block;
  width: 100%;
}
.sidenav-menu .dez-social-icon.social-2 {
  margin: 0;
}
.sidenav-menu .dez-social-icon.social-2 li a {
  color: #000;
  font-family: "Lora", serif;
  text-transform: capitalize;
  font-weight: 400;
  writing-mode: vertical-rl;
  padding: 10px 10px;
  margin: 0;
  display: block;
}
/* nav */
.menuleft {
  width: 100%;
  background: #fff;
  height: 100%;
  position: fixed;
  z-index: 999;
  left: -100%;
  top: 0;
  display: flex;
  align-items: center;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.menuleft:after {
  content: "";
  height: 100%;
  width: 0;
  background: #ff6977;
  top: 0;
  z-index: -1;
  position: absolute;
  left: 0;
  transition: all 0.5s ease 0.5s;
}
.menuleft.active:after {
  width: 100%;
}
.menuleft .close-btn {
  position: absolute;
  right: 40px;
  top: 40px;
  font-size: 70px;
  font-weight: 100;
  color: #fff;
  line-height: 40px;
  cursor: pointer;
}
.menuleft.active {
  left: 0;
}

.menuleft .nav {
  padding: 0 80px;
}
.menuleft .nav li {
  display: block;
  width: 100%;
}
.menuleft .nav li a {
  color: #fff;
  display: inline-block;
  font-size: 70px;
  padding: 15px 0;
  line-height: 80px;
  position: relative;
  z-index: 1;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.menuleft .nav li a:after {
  content: "";
  width: 0;
  height: 6px;
  position: absolute;
  left: 0;
  top: 50%;
  background: #fff;
  opacity: 1;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.menuleft .nav li a:hover:after {
  width: 100%;
}
.menuleft .nav li a {
  padding-left: 20px;
}
/* ===============================
	about-sidebox
================================ */
.about-sidebox {
  position: absolute;
  top: 0;
  height: 100vh;
  width: 100vw;
  left: -100%;
  display: block;
  overflow: hidden;
  margin-top: 0;
  z-index: 9999;
  background: #fff;
  transition: all 0.8s;
}
.about-sidebox.active {
  left: 0;
}
.about-sidebox:after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: 0;
  z-index: -1;
  left: 0;
  right: 100%;
  transition: all 0.5s;
  background: #000;
}
.about-sidebox.active:after {
}
.contact-box {
  max-width: 50vw;
  height: 100vh;
  background: #e4e8f3;
}
.about-sidebox .closebtn {
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  z-index: 2;
  background: #5362ff;
  font-size: 30px;
  font-weight: 200;
  line-height: 50px;
  text-align: center;
  color: #fff;
}
.about-sidebox .closebtn:hover {
  background: #5462ff;
  color: #fff;
}
.about-section {
  display: flex;
}
.contact-box .contact-form {
  padding: 80px 80px;
  height: 70vh;
  position: relative;
  z-index: 1;
}
.contact-box .form-group {
  position: relative;
  z-index: 1;
}
.contact-box .form-group label {
  color: #000;
  font-weight: 400;
  font-size: 15px;
  position: absolute;
  left: 0px;
  top: 15px;
  margin: 0;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.contact-box .form-group.focused label {
  font-size: 13px;
  top: -10px;
  color: #000;
  font-weight: 600;
}
.contact-box input.form-control {
  height: 50px;
}
.contact-box textarea.form-control {
  height: 240px;
}
.contact-box .form-control {
  border: 1px solid #cccccc;
  border-radius: 0;
  border-width: 0 0 1px 0;
  padding: 15px 0;
  background: transparent;
  position: relative;
}
.contact-box .form-group:after {
  content: "";
  background-image: linear-gradient(
    to right,
    #2e79ff 0,
    #5462ff 51%,
    #2e79ff 100%
  );
  height: 2px;
  width: 100%;
  z-index: 1;
  position: absolute;
  bottom: 0;
  box-shadow: 0px 5px 10px 0 rgba(47, 120, 255, 0.35) !important;
  background-size: 200%;
}
.contact-info {
  background: #e4e8f3;
  height: 30vh;
  display: flex;
}
.contact-info .col:first-child {
  background: #eef2fb;
}
.contact-info .col {
  padding: 60px 80px;
  max-width: 50%;
  flex: 0 0 50%;
}
.contact-info .col .title {
  margin: 0 0 10px 0;
  font-size: 22px;
  color: #212328;
  font-weight: 600;
  line-height: 28px;
}
.contact-info .col a,
.contact-info .col p {
  color: #717171;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
.contact-form .title {
  position: absolute;
  right: 0;
  margin: 0;
  color: #000;
  text-transform: uppercase;
  writing-mode: vertical-lr;
  padding: 0px 15px;
  font-size: 16px;
  letter-spacing: 2px;
}
.contact-form .title:after {
  content: "";
  height: 40px;
  width: 1px;
  position: absolute;
  background: #000;
  bottom: -50px;
  left: 50%;
}
.gallery-area {
  width: 50vw;
  height: 100vh;
  background: #212328;
  padding: 80px;
}
.gallery-box {
  margin-bottom: 30px;
}
.gallery-box .gallery-media {
  border-radius: 8px;
  overflow: hidden;
}
.gallery-box .gallery-media img {
  transform: scale(1.15);
  -moz-transform: scale(1.15);
  -webkit-transform: scale(1.15);
  -ms-transform: scale(1.15);
  transform: scale(1.15);
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.gallery-box:hover .gallery-media img {
  transform: scale(1);
  -moz-transform: scale(1);
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}
.gallery-area .mCSB_dragger {
  background-color: rgba(0, 0, 0, 0.2);
}
.contact-info .col,
.gallery-area {
  clip-path: polygon(0% 100%, 0% 0%, 0% 0%, 0% 100%);
  -ms-clip-path: polygon(0% 100%, 0% 0%, 0% 0%, 0% 100%);
  -moz-clip-path: polygon(0% 100%, 0% 0%, 0% 0%, 0% 100%);
  -webkit-clip-path: polygon(0% 100%, 0% 0%, 0% 0%, 0% 100%);
  transition: all 0.8s ease 0.8s;
}
.about-sidebox.active .contact-info .col,
.about-sidebox.active .gallery-area {
  clip-path: polygon(100% 100%, 100% 0%, 0% 0%, 0% 100%);
  -ms-clip-path: polygon(100% 100%, 100% 0%, 0% 0%, 0% 100%);
  -moz-clip-path: polygon(100% 100%, 100% 0%, 0% 0%, 0% 100%);
  -webkit-clip-path: polygon(100% 100%, 100% 0%, 0% 0%, 0% 100%);
}
.contact-form {
  background: #fff;
  clip-path: polygon(100% 100%, 100% 100%, 0% 100%, 0% 100%);
  -ms-clip-path: polygon(100% 100%, 100% 100%, 0% 100%, 0% 100%);
  -moz-clip-path: polygon(100% 100%, 100% 100%, 0% 100%, 0% 100%);
  -webkit-clip-path: polygon(100% 100%, 100% 100%, 0% 100%, 0% 100%);
  transition: all 0.8s ease 1s;
}
.about-sidebox.active .contact-form {
  clip-path: polygon(100% 100%, 100% 0%, 0% 0%, 0% 100%);
  -ms-clip-path: polygon(100% 100%, 100% 0%, 0% 0%, 0% 100%);
  -moz-clip-path: polygon(100% 100%, 100% 0%, 0% 0%, 0% 100%);
  -webkit-clip-path: polygon(100% 100%, 100% 0%, 0% 0%, 0% 100%);
}
/* Subscribe */
.subscribe-box .modal-content {
  background: #000000;
  padding: 60px 40px;
  border-radius: 20px;
  max-width: 550px;
  width: 550px;
  border: 0;
  text-align: center;
}
.subscribe-box .modal-header {
  background: transparent;
  flex-direction: column;
  text-align: center;
  padding: 0;
  border: 0;
}
.subscribe-box .modal-header .modal-title {
  color: #ffffff;
  margin: 0 0 0 0;
  font-size: 32px;
  font-weight: 600;
  display: block;
  width: 100%;
}
.subscribe-box .modal-header p {
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  margin-bottom: 35px;
}
.subscribe-box .modal-header .close {
  margin: 0;
  font-weight: 200;
  font-size: 45px;
  opacity: 0.5;
  position: absolute;
  line-height: 45px;
  padding: 0;
  right: 25px;
  top: 20px;
}
.subscribe-box .modal-body {
  padding: 0;
  color: #fff;
  text-align: center;
}
.subscribe-box .modal-footer {
  padding: 0;
  border: 0;
  text-align: center;
  display: block;
}
.subscribe-box .form-control {
  border: 2px solid #000000;
  height: 50px;
  font-size: 16px;
  font-weight: 400;
  padding: 10px 20px;
  border-radius: 8px;
}
.subscribe-box .modal-header img {
  margin: -10px auto -5px;

  -webkit-transform-origin: 50% 4px;

  -moz-transform-origin: 50% 4px;
}
/* bell */
@-webkit-keyframes ring {
  0% {
    -webkit-transform: rotateZ(0);
  }
  1% {
    -webkit-transform: rotateZ(30deg);
  }
  3% {
    -webkit-transform: rotateZ(-28deg);
  }
  5% {
    -webkit-transform: rotateZ(34deg);
  }
  7% {
    -webkit-transform: rotateZ(-32deg);
  }
  9% {
    -webkit-transform: rotateZ(30deg);
  }
  11% {
    -webkit-transform: rotateZ(-28deg);
  }
  13% {
    -webkit-transform: rotateZ(26deg);
  }
  15% {
    -webkit-transform: rotateZ(-24deg);
  }
  17% {
    -webkit-transform: rotateZ(22deg);
  }
  19% {
    -webkit-transform: rotateZ(-20deg);
  }
  21% {
    -webkit-transform: rotateZ(18deg);
  }
  23% {
    -webkit-transform: rotateZ(-16deg);
  }
  25% {
    -webkit-transform: rotateZ(14deg);
  }
  27% {
    -webkit-transform: rotateZ(-12deg);
  }
  29% {
    -webkit-transform: rotateZ(10deg);
  }
  31% {
    -webkit-transform: rotateZ(-8deg);
  }
  33% {
    -webkit-transform: rotateZ(6deg);
  }
  35% {
    -webkit-transform: rotateZ(-4deg);
  }
  37% {
    -webkit-transform: rotateZ(2deg);
  }
  39% {
    -webkit-transform: rotateZ(-1deg);
  }
  41% {
    -webkit-transform: rotateZ(1deg);
  }

  43% {
    -webkit-transform: rotateZ(0);
  }
  100% {
    -webkit-transform: rotateZ(0);
  }
}

